.vc-dialogue {
  z-index: 21;
}
.vc-dialogue .dialogue-hd {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 44px;
